
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {localapi, proapi, imgBaseUrl} from '../../config/env'
import {mobileCode, checkExsis, sendLogin, getcaptchas, accountLogin} from '../../service/getData'

export default {
components: {
Group,
XInput,
XButton,
Blur,
Divider,
Flexbox,
FlexboxItem
},
data () {
return {
title: '欢迎登录',
loginSrc:require('../../assets/logo.png'),
loginWay: false, //登录方式，默认短信登录
showPassword: false, // 是否显示密码
phoneNumber: null, //电话号码
mobileCode: null, //短信验证码
validate_token: null, //获取短信时返回的验证值，登录时需要
computedTime: 0, //倒数记时
userInfo: null, //获取到的用户信息
userAccount: null, //用户名
passWord: null, //密码
captchaCodeImg: null, //验证码地址
codeNumber: null, //验证码
showAlert: false, //显示提示组件
alertText: null //提示的内容
}
},
created: function() {

},
methods: {
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
var url = 'http://celelink.com:8088/cum/backend/api/address/getUserAddress'
this.$ajax({
type:'post',
url:url,
'userName':'xuwen',
'password':'123'
})
.then(function (data) {
console.log(data.request)
})
setTimeout(() => {
this.$vux.loading.hide()
}, 20000)
}
},
mounted () {
let w = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
this.$refs.nana.style.height = h +'px';
this.$refs.nana.style.width = w +'px';
}
}



